	module.exports = {"data":{"webpagesPage":{"childMarkdownRemark":{"html":"<p>Vi hjelper deg med din tilstedeværelse på nett. Vi lager dine nye websiders, med det innholdet du ønsker.</p>\n<p>Vi behersker de fleste moderne web-teknologier, og gir deg gode råd på veien for at du skal få det resultatet du ønsker.</p>\n<p>Websider som fungerer på alle typer apparater er en selvfølge. Ekstra innsats på søkemotoroptimalisering kan vi hjelpe til med hvis ønskelig. Kobling til sosiale medier som Facebook, Instagram og Twitter kan du få. Det samme gjelder bistand til annonsering på Facebook eller Google.</p>\n<p>Ta kontakt og fortell hva du ønsker, så starter vi en dialog om hvordan du kan nå dine mål på nett.</p>","frontmatter":{"header":"Vi utvikler profesjonelle nettsider","checklistHeader":"Våre nettsider leveres alltid med:","checklist":["Responsivt design","Mobil tilpassing","Rask webhosting","SEO-vennlighet","Mulighet for endring av innhold selv"],"portfolioHeader":"Nettsider utviklet av våre medarbeidere","portfolio":[{"title":"Stølsrock 2017","img":"","description":"Konsert arrangement i Hemsedal","url":"http://stolsrock.no/"},{"title":"Fargerike Hemsedal","img":"","description":"Farge og interiør-butikk i Hemsedal","url":"http://www.fargerikehemsedal.no/"},{"title":"Hallingdal Næringshage","img":"","description":"Utviklings- og kompetansesenter for næringslivet i Hallingdal","url":"http://nhage.no/"},{"title":"Smart Dekk","img":"","description":"Dekk og felg butikk","url":"http://smartdekk.no/"},{"title":"Saga bil","img":"","description":"Bil butikk","url":"http://sagabil.no/"},{"title":"Haraldset Foto","img":"","description":"Naturfotograf","url":"http://haraldset.com/"}]}}}},"pathContext":{}}

